%box-s {
  box-sizing: border-box;
}

nav {
  margin: 0;
  padding: 0;
  margin-block-start: 2rem;
  margin-block-end: 2rem;
  margin-inline-start: 2rem;
  margin-inline-end: 2rem;
  position: relative;
  @extend %box-s;
}

h1 {
  margin: 2rem;
  text-align: center;
  border-bottom: 2px solid blueviolet;
  padding-bottom: 12px;
  @extend %box-s;
}

.img-page-1 {
  width: 100%;
  @extend %box-s;
}

.sim-slider {
  width: 500px;
  margin: 20px auto;
  padding: 30px 50px;
  border: 1px solid #ccd;
  background-color: white;
  display: block;
  position: relative;
  left: -25%;
  @extend %box-s;
}

.sim-slider-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  @extend %box-s;
}

.sim-slider-element {
  width: 100%;
  transition: opacity 1s ease-in;
  opacity: 0;
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  display: block;
  @extend %box-s;
}

.sim-slider-arrow-left,
.sim-slider-arrow-right {
  width: 22px;
  height: 40px;
  position: absolute;
  cursor: pointer;
  opacity: 0.6;
  z-index: 4;
  @extend %box-s;
}

.sim-slider-arrow-left {
  left: 10px;
  top: 40%;
  display: block;
  background: url("http://pvbk.spb.ru/inc/slider/sim-files/sim-arrow-left.png")
    no-repeat;
  @extend %box-s;
}

.sim-slider-arrow-right {
  right: 10px;
  top: 40%;
  display: block;
  background: url("http://pvbk.spb.ru/inc/slider/sim-files/sim-arrow-right.png")
    no-repeat;
  @extend %box-s;
}

.sim-slider-arrow-left:hover {
  opacity: 1;
  @extend %box-s;
}

.sim-slider-arrow-right:hover {
  opacity: 1;
  @extend %box-s;
}

.subtitle {
  display: block;
  position: absolute;
  width: 100%;
  top: 0%;
  left: 110%;
  @extend %box-s;
}

.section {
  display: block;
  margin: 0;
  padding: 0;
  margin-block-start: 2rem;
  margin-block-end: 2rem;
  margin-inline-start: 2rem;
  margin-inline-end: 2rem;
  position: relative;
  @extend %box-s;
}

.img-page-2 {
  width: 40%;
  height: 40%;
  @extend %box-s;
}

.subtitle1 {
  display: block;
  position: absolute;
  width: 50%;
  top: 0%;
  left: 45%;
  @extend %box-s;
}

.subtitle2 {
  display: block;
  position: absolute;
  width: 50%;
  top: 26%;
  left: 45%;
  @extend %box-s;
}

.subtitle3 {
  display: block;
  position: absolute;
  width: 50%;
  top: 52%;
  left: 45%;
  @extend %box-s;
}

.img-page-3 {
  width: 40%;
  height: 40%;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  @extend %box-s;
}

.h4-page-4 {
  margin: 0;
  @extend %box-s;
}

.input {
  margin-bottom: 1rem;
  @extend %box-s;
}

.input-message {
  margin-bottom: 1rem;
  margin-right: 30rem;
  height: 5rem;
  width: 50%;
  @extend %box-s;
}

.footer {
  display: block;
  margin: 0;
  padding: 0;
  margin-block-start: 2rem;
  margin-block-end: 2rem;
  margin-inline-start: 2rem;
  margin-inline-end: 2rem;
  position: relative;
  @extend %box-s;
}
